*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    font-size: 1.6rem;
    font-family: 'Inter', sans-serif;
}

body,
html{
    width: 100%;
    height: 100%;
    font-size: 62.5%;
    background-color: black;
}

img{
    max-width: 100%;
}

button{
    cursor: pointer;
}